
$extra-padding: 40px;

.padding-xs-left {
  padding-left: $extra-padding;
}

.padding-xs-right {
  padding-right: $extra-padding
}

@include respond($sm-up) {
  .padding-sm-left {
    padding-left: $extra-padding;
  }

  .padding-sm-right {
    padding-right: $extra-padding
  }
}

@include respond($md-up) {
  .padding-md-left {
    padding-left: $extra-padding;
  }

  .padding-md-right {
    padding-right: $extra-padding
  }
}

@include respond($lg-up) {
  .padding-lg-left {
    padding-left: $extra-padding;
  }

  .padding-lg-right {
    padding-right: $extra-padding
  }
}