$type-mod: .75;

.jumbotron {
  background-size: cover;
  background-position: center 30%;

  h1,
  h2 {
    font-size: $font-size-h1 * $type-mod;
    margin-top: 0;
  }

  &.jumbotron-inverse {
    background-color: $jumbotron-inverse-bg;
    color: $jumbotron-inverse-color;

    h1 {
      color: $jumbotron-inverse-heading-color;
    }
  }

  &.jumbotron-image {
    background-color: transparent;
  }

  // background-color
  .jumbotron-bg {
    z-index: 1;
    padding: 15px;
    position: relative;

    & > * {
      position: relative;
      z-index: 1;
      margin-bottom: 0;

      &:first-child {
        margin-top: 0;
      }
    }

    &:before {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0.9;
      border-radius: $border-radius-base;
      background-color: $gray-lighter;
    }
  }

  &.jumbotron-inverse {
    .jumbotron-bg:before {
      background-color: $black;
      opacity: .6;
    }
  }
}

  
.full-container {
  margin-bottom: 30px;

  .jumbotron { 
    padding-left:  ($jumbotron-padding/2);
    padding-right: ($jumbotron-padding/2);
    margin-bottom: 0;
  }
}

@include respond($sm-up) {
  
  .jumbotron {

    h1,
    h2 {
      font-size: $font-size-h1;
    }
    
    .jumbotron-bg {
      padding: 50px;
    }
    
  }

  .full-container {
    
    .jumbotron { 
      padding-left:  ($jumbotron-padding * 2);
      padding-right: ($jumbotron-padding * 2);

      .container & {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}