/* Have to set height explicity on ui-view 
to prevent collapsing during animation*/
.animation-wrap {
  position: relative;
  overflow: hidden; }

[ui-view].transform {
  z-index: 2;
  min-height: 200px; }

.animate-height-fake {
  min-height: 90%; }

.transform.ng-enter, .transform.ng-enter-active, .transform.ng-leave, .transform.ng-leave-active, .slidedown.ng-enter, .slidedown.ng-enter-active, .slidedown.ng-leave, .slidedown.ng-leave-active {
  overflow: hidden; }

.transform.ng-enter, .slidedown.ng-enter {
  z-index: 2; }

.transform.ng-leave, .slidedown.ng-leave {
  z-index: 1; }

@media screen and (max-width: 767px) {
  .transform.ng-enter, .transform.ng-leave, .transform.ng-hide-add, .transform.ng-hide-remove,
  .slidedown.ng-enter,
  .slidedown.ng-leave,
  .slidedown.ng-hide-add,
  .slidedown.ng-hide-remove {
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out; }
  .transform.ng-enter, .transform.ng-hide-add,
  .slidedown.ng-enter,
  .slidedown.ng-hide-add {
    position: relative;
    opacity: 0; }
  .transform.ng-enter.ng-enter-active, .transform.ng-hide-add.ng-hide-add-active,
  .slidedown.ng-enter.ng-enter-active,
  .slidedown.ng-hide-add.ng-hide-add-active {
    opacity: 1; }
  .transform.ng-leave, .transform.ng-hide-remove,
