
// Remove lines between trs
.table {
  border: 1px solid $table-border-color;
  // Cells
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        border-top: 0;
      }
    }
  }
}


// @todo: mixin throws errors
