$toolbar-bg: $gray-dark;
$toolbar-second-bg: $gray-base;
$toolbar-active-bg: darken($toolbar-bg, 5%);


// Default navbar
.navbar-proud {
  background-color: $toolbar-bg;
  border-color: $toolbar-bg;
  border-bottom-color: $gray-darker;

  .navbar-header {
    margin-right: 20px;
  }

  .proud-menu-tab a {
    background-color: $toolbar-second-bg!important;

    &:hover, &.active {
      background-color: $black!important;
    }
  }

  .navbar-brand {
    color: $navbar-default-brand-color;
    &:hover,
    &:focus {
      color: $navbar-default-brand-hover-color;
      background-color: $navbar-default-brand-hover-bg;
    }
    padding-right: 0;

    img {
      border-radius: $border-radius-base;
    }
  }

  i.fa {
    padding-right: 5px;
  }

  .navbar-text {
    color: $navbar-default-color;
  }

  .navbar-nav {
    > li > a {
      color: $navbar-default-link-color;

      &:hover,
      &:focus {
        color: $white;
        background-color: $toolbar-active-bg;
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        color: $white;
        background-color: $toolbar-active-bg;
      }
    }
    > .disabled > a {
      &,
      &:hover,
      &:focus {
        color: $navbar-default-link-disabled-color;
        background-color: $navbar-default-link-disabled-bg;
      }
    }
  }

  .navbar-toggle {
    border-color: $navbar-default-toggle-border-color;
    &:hover,
    &:focus {
      background-color: $toolbar-active-bg;
    }
    .icon-bar {
      background-color: $navbar-default-toggle-icon-bar-bg;
    }
  }

  .navbar-collapse,
  .navbar-form {
    border-color: $navbar-default-border;
  }

  // Dropdown menu items
  .navbar-nav {
    // Remove background color from open dropdown
    > .open > a {
      &,
      &:hover,
      &:focus {
        background-color: $toolbar-active-bg;
        color: $white;
      }
    }
  }
}

// Mobile only
// -------------------------
@media (max-width: $grid-float-breakpoint-max) {

  // Default navbar
  .navbar-proud {
    .navbar-header {
      margin-right: 0px;
    }

    // Dropdown menu items
    .navbar-nav {
      // Dropdowns get custom display when collapsed
      .open .dropdown-menu {
        > li > a {
          color: $navbar-default-link-color;
          &:hover,
          &:focus {
            color: $white;
            background-color: $navbar-default-link-hover-bg;
          }
        }
        > .active > a {
          &,
          &:hover,
          &:focus {
            color: $navbar-default-link-active-color;
            background-color: $navbar-default-link-active-bg;
          }
        }
        > .disabled > a {
          &,
          &:hover,
          &:focus {
            color: $navbar-default-link-disabled-color;
            background-color: $navbar-default-link-disabled-bg;
          }
        }
      }
    }
  }
}